import {Map} from 'immutable';

//ì•¡ì…˜ ì´
const LOGIN = 'LOGIN';
const LOGOUT = 'LOGOUT';

//
export const login ={
    type: LOGIN
};
export const logout ={
    type: LOGOUT
};

export const initialState = {
    user: null
};

//ë¦¬ë“€ì„œ
const handleActions = (state = initialState, action) => {
    switch (action.type) {
        case LOGIN:
            return {
                user: action.user,
            };
            break;
        case LOGOUT:
            return {
                user: action.user,
            };
            break;
        default:
            return state;
    }
};

export default handleActions;