import React from 'react';
import * as firebase from "firebase";

const Delete=(props)=>{

    const onDelete=(e)=>{
        e.preventDefault();

        const uid = JSON.parse(localStorage.getItem('logInfo')).user.uid;
        const database = firebase.database();

        database.ref(`mandal/${uid}`).once('value', (snapshot) => {
            let obj = snapshot.val();
            let keyList = [];

            //í‚¤ê°’ ì°¾ê¸°
            for (let key in obj) {
                if (obj.hasOwnProperty(key)) {
                    keyList.push(key);
                    // console.log(key);
                }
            }
            console.log(keyList[props.pageNo]);
            //ì‚­ì œ
            database.ref(`mandal/${uid}/${keyList[props.pageNo]}`).remove();
        });
        setTimeout(()=>{
            window.location.href='/list';
        },1000);
    }
    return(
        <button className="btn delete" onClick={onDelete}>ì‚­ì œ</button>
    );

}

export default Delete;